import React from "react";
import Obfuscate from "../../components/obfuscate.js";

function Appearance() {
  const [currentTheme, setCurrentTheme] = React.useState(localStorage.getItem("appearance") || "default")
  function themeClass(type) {
    return currentTheme == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setTheme(event) {
    localStorage.setItem("appearance", event.target.dataset.tab)
    setCurrentTheme(event.target.dataset.tab)
    document.body.setAttribute("appearance", event.target.dataset.tab || "default")
  }
  const [currentBackground, setCurrentBackground] = React.useState(localStorage.getItem("background") || "none")
  function backgroundClass(type) {
    return currentBackground == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setBackground(event) {
    localStorage.setItem("background", event.target.dataset.tab)
    setCurrentBackground(event.target.dataset.tab)
    window.dispatchEvent(new Event("updateBackground"))
  }
  return (
      <React.Fragment>
      <div className="optiontitle"><Obfuscate>Theme</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="default" onClick={setTheme} className={themeClass("default")}><Obfuscate>Default</Obfuscate></div>
      <div data-tab="dark" onClick={setTheme} className={themeClass("dark")}><Obfuscate>Dark</Obfuscate></div>
      <div data-tab="light" onClick={setTheme} className={themeClass("light")}><Obfuscate>Light</Obfuscate></div>
      <div data-tab="lime" onClick={setTheme} className={themeClass("lime")}><Obfuscate>Lime</Obfuscate></div>
      <div data-tab="space" onClick={setTheme} className={themeClass("space")}><Obfuscate>Space</Obfuscate></div>
      <div data-tab="molten" onClick={setTheme} className={themeClass("molten")}><Obfuscate>Molten</Obfuscate></div>
      <div data-tab="retro" onClick={setTheme} className={themeClass("retro")}><Obfuscate>Retro</Obfuscate></div>
      <div data-tab="eaglenet" onClick={setTheme} className={themeClass("eaglenet")}><Obfuscate>EagleNet</Obfuscate></div>
      <div data-tab="midnight" onClick={setTheme} className={themeClass("midnight")}><Obfuscate>Midnight</Obfuscate></div>
      <div data-tab="honey" onClick={setTheme} className={themeClass("honey")}><Obfuscate>Honey</Obfuscate></div>
      <div data-tab="simple" onClick={setTheme} className={themeClass("simple")}><Obfuscate>Simple</Obfuscate></div>
      <div data-tab="tsunami" onClick={setTheme} className={themeClass("tsunami")}><Obfuscate>Tsunami</Obfuscate></div>
      </div>
      <div className="optiontitle"><Obfuscate>Background</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="none" onClick={setBackground} className={backgroundClass("none")}><Obfuscate>None</Obfuscate></div>
      <div data-tab="particles" onClick={setBackground} className={backgroundClass("particles")}><Obfuscate>Particles</Obfuscate></div>
      <div data-tab="stars" onClick={setBackground} className={backgroundClass("stars")}><Obfuscate>Stars</Obfuscate></div>
      </div>
      </React.Fragment>
  );
}

export default Appearance;