import React from "react";
import { Link } from "react-router-dom";
import Nav from "../components/nav.js";
import Obfuscate from "../components/obfuscate.js";
import Head from "../components/head.js";
import Proxy from "../components/proxy.js";

function Home() {
  var [proxyConfig, setProxyConfig] = React.useState({
    open: false,
    url: null,
    title: null,
    icon: null
  })
  function searchType(e) {
    var getLink = window['getLink']
    if (e.keyCode === 13) {
      setProxyConfig({
        open: true,
        url: getLink(e.target.value),
        title: null,
        icon: null
      })
      e.target.value = ""
    }
  }
  return (
      <React.Fragment>
      <Head defaultTitle="Metallic">
      <script src="./js/go.js"></script>
      </Head>
      <Proxy config={proxyConfig} configChange={(e) => setProxyConfig(e)} />
      <Nav />
      <div className="hometitle"><Obfuscate>Metallic</Obfuscate></div>
      <div className="omniboxcontainer">
      <div className="omnibox">
      <div className="searchicon"><svg focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M15.5 14h-.79l-.28-.27A6.471 6.471 0 0 0 16 9.5 6.5 6.5 0 1 0 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"></path></svg></div>
      <input onKeyUp={searchType} autoComplete="off" autoFocus id="search" className="search" />
      </div>
      <div className="suggestions">
      <div className="sugg">Coming Soon!</div>
      </div>
      </div>
      <div className="searchbtns">
      <Link to="/apps"><button className="searchbtn"><Obfuscate>Apps</Obfuscate></button></Link>
      <Link to="/games"><button className="searchbtn"><Obfuscate>Games</Obfuscate></button></Link>
      </div>
      <div className="footer">
      <Obfuscate> Metallic {new Date().getFullYear()}</Obfuscate>
      </div>
      </React.Fragment>
  );
}

export default Home;